.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 36px;
}

.card__title {
  color: #060b35;
  margin: 4px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews--value {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 0;
  display: flex;
}

.card__price--text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price--value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-top: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
}

.card__button:hover {
  box-sizing: border-box;
  color: var(--blue-accent);
  background: #fff;
  border: 1px solid #00acdc;
}

.page {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.stars {
  justify-content: flex-end;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
}

